/** @format */
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  box-sizing: border-box;
  height: 100vh;
}

header {
  /* border: 2px solid black; */
  height: 100vh;
  width: 100%;
  position: relative;
  background: #009688;
}

nav {
  /* border: 2px solid black; */
  box-shadow: 1.5px 2px 3px 1px rgb(82, 79, 79);
  padding: 20px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  background: #fff;
  width: 100%;
  height: 100px;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
  position: fixed;
  top: 0;
}
.logo {
  background: white;
  border-radius: 7px;
  box-shadow: 1.5px 2.3px 3px rgb(0, 0, 0);
}
.logo h1 {
  font-size: 50px;
  font-weight: bold;
  text-shadow: 1px 2px 3px rgb(97, 97, 97);
  color: #014201;
  font-family: monospace;
}
.menu {
  background: linear-gradient(rgba(0, 0, 0, 0.5), #009688);
  border-radius: 7px;
  margin-right: 60px;
  /* position: absolute; */
  /* right: -455px; */
  transition: 1s;
}

.menu ul {
  display: flex;
  height: 50px;
  align-items: center;
}
.menu ul li {
  list-style: none;
  transition: 0.5s;
  margin: 0 20px;
  transition: 0.5s;
}
.menu ul li a {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 20px;
  color: white;
}
button {
  height: 50px;
  width: 50px;
  border: 2px solid white;
  background-color: black;
  position: absolute;
  right: 130px;
  cursor: pointer;
}

.menu ul li:hover {
  transform: translateY(-7px);
}

/* welcome */
.container {
  /* border: 2px solid red; */

  text-align: center;
  height: -webkit-fill-available;
  padding: 100px 50px;
  margin: auto;
  margin-top: 100px;
}
.welcome {
  margin-top: 100px;
}

.welcome h2 {
  font-size: 50px;
}
.welcome p {
  font-size: 20px;
}

.getting {
  border: 2px solid black;
  border-radius: 7px;
  margin: 30px auto;
  padding: 25px 0;
  width: 800px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  padding: 10px 10px;
}
.getting li {
  font-size: 20px;
  line-height: 15px;
  padding: 10px 5px;
}

@media screen and (min-width: 250px) and (max-width: 500px) {
  /* nav */
  nav {
    padding-left: 10px;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    height: 160px;
  }
  .logo h1 {
    font-size: 35px;
  }
  button {
    position: absolute;
    right: 60px;
  }
  .menu {
    height: 200px;
    width: 200px;
    margin-top: 0px;
  }
  .menu ul {
    flex-direction: column;
    text-align: center;

    height: 0;
  }
  .menu li a {
    font-size: 10px;
  }
  .menu ul li a:hover {
    color: black;
  }
  .menu ul li:hover {
    transform: none;
  }

  /* container */
  .container {
    padding: 50px 0px;
  }
  .welcome h2 {
    font-size: 30px;
  }
  .welcome p {
    font-size: 20px;
  }
  .getting {
    flex-wrap: wrap;
    flex-direction: column;
    width: auto;
    margin-top: 40px;
  }
  .getting ol {
    font-size: 15px;
    line-height: 30px;
    /* list-style: square; */
  }
}
@media screen and (min-width: 500px) and (max-width: 850px) {
  /* nav */
  .logo h1 {
    font-size: 45px;
  }
  .menu {
    margin-top: 10px;
  }
  .menu ul li a {
    font-size: 15px;
  }
  .menu ul li {
    margin: 0 15px;
  }

  /* container */
  .welcome h2 {
    font-size: 30px;
  }
  .getting {
    width: auto;
    flex-direction: column;
  }
}

@media screen and (min-width: 850px) and (max-width: 970px) {
  /* nav */
  .logo h1 {
    font-size: 45px;
  }
  .menu ul li a {
    font-size: 18px;
  }
}

